@import url("https://fonts.googleapis.com/css2?family=Playfair:wght@400;900&display=swap");

:root {
    --nav-height: clamp(3.5rem, 5vw, 8rem);
    --main-side-padding: clamp(1rem, 6vw, 20rem);
    --primary: hsl(0, 0%, 100%);
    --secondary: hsl(0, 0%, 0%);
    --tertiary: hsl(0, 0%, 90%);
    --theme-font-head: hsl(0, 0%, 0%);
    --theme-font-body: hsl(0, 0%, 20%);
    --yellow: hsl(45, 100%, 65%);
    --yellow-2: hsl(48, 100%, 90%);
    --yellow-3: hsl(50, 100%, 95%);
    --blue: hsl(191, 87%, 41%);
    --instagram: linear-gradient(
        45deg,
        hsl(280, 100%, 60%),
        hsl(300, 100%, 80%),
        hsl(320, 100%, 50%)
    );
}

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    min-height: 100vh;
    overflow-x: hidden;
    font-family: Arial, sans-serif;
}

nav {
    z-index: 999;
    display: flex;
    height: 6rem;
    width: 100%;
    background-color: white;
    align-items: center;
    justify-content: space-between;
    transition: width 300ms ease;
    position: sticky;
    top: 0;
    padding-inline: 2rem;
}

nav > .routes {
    display: flex;
    width: 100%;
    height: auto;
    justify-content: center;
}

nav > .routes > button {
    padding: 1.25rem;
    --outline-color: none;
    height: 100%;
    aspect-ratio: 1;
    background: none;
    color: white;
    position: relative;
    border: none;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
}

nav > .routes > button.active,
nav > .routes > button:hover {
    --outline-color: cyan;
}

nav > .routes > button::before {
    bottom: 0;
    content: "";
    position: absolute;
    width: 100%;
    height: 0.1rem;
    background-color: var(--outline-color);
    transition: background-color 300ms ease;
}

.no-scroll {
    overflow: hidden;
}

.hidden {
    display: none;
}
